import { Component, OnInit, Input } from '@angular/core';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { humaniseDuration } from 'src/app/shared/utilities/general.utilities';
import { MatDialog } from '@angular/material/dialog';
import { PrivateDetailsModalComponent } from '../../private-details-modal/private-details-modal.component';

@Component({
    selector: 'meeting-private-details',
    templateUrl: './private-details.component.html',
    styleUrls: ['./private-details.component.scss'],
})
export class MeetingPrivateDetailsComponent implements OnInit {
    /** Booking to display details */
    @Input() public booking: Booking;

    public get setup(): string {
        let setup = this.booking.setup[this.booking.space.email];
        if (setup > 120) {
            setup = Math.floor(setup / 60);
        }
        return setup ? humaniseDuration(setup) : '<No Setup time>';
    }

    public get breakdown(): string {
        let breakdown = this.booking.breakdown[this.booking.space.email];
        if (breakdown > 120) {
            breakdown = Math.floor(breakdown / 60);
        }
        return breakdown ? humaniseDuration(breakdown) : '<No Breakdown time>';
    }

    public get notes() {
        return '';
    }

    constructor(private _dialog: MatDialog) {}

    ngOnInit(): void {}

    public openPrivateDetailsModal() {
        this._dialog.open(PrivateDetailsModalComponent, {
            data: {
                booking: this.booking,
            },
        });
    }
}
